/* Сброс стилей для компонента блога */
.blog-slider-main * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.blog-slider-main {
  position: relative;
  width: 100vw;
  height: 90vh;
  object-fit: cover;
}

.slider {
  top: -180px;
  list-style: none;
  padding: 0;
  margin: 0;
  position: relative;
  width: 100%;
  height: 100%;
}

.item {
  width: 200px;
  height: 300px;
  list-style-type: none;
  position: absolute;
  top: 60%;
  transform: translateY(-50%);
  z-index: 1;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 20px;
  box-shadow: 0 20px 30px rgba(255,255,255,0.3) inset;
  transition: transform 0.1s, left 0.75s, top 0.75s, width 0.75s, height 0.75s;
}

.item:nth-child(1), 
.item:nth-child(2) {
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  transform: none;
  border-radius: 0;
  box-shadow: none;
  opacity: 1;
 
}

.item:nth-child(3) { 
  left: 50%; 
}

.item:nth-child(4) { 
  left: calc(50% + 220px); 
}

.item:nth-child(5) { 
  left: calc(50% + 440px); 
}

.item:nth-child(6) { 
  left: calc(50% + 660px); 
  opacity: 0; 
}

.content {
  width: min(30vw, 400px);
  position: absolute;
  top: 60%;
  left: 10rem;
  transform: translateY(-50%);
  font: 400 0.85rem helvetica, sans-serif;
  color: white;
  text-shadow: 0 3px 8px rgba(0,0,0,0.5);
  opacity: 0;
  display: none;
}

.content .title {
  font-family: 'arial-black', Arial, sans-serif;
  text-transform: uppercase;
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.content .description {
  line-height: 1.7;
  margin: 1rem 0 1.5rem;
  font-size: 0.8rem;
}

.content button {
  width: fit-content;
  background-color: rgba(0,0,0,0.1);
  color: white;
  border: 2px solid white;
  border-radius: 0.25rem;
  padding: 0.75rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.content button:hover {
  background-color: rgba(255,255,255,0.2);
}

.item:nth-of-type(2) .content {
  display: block;
  animation: show 0.75s ease-in-out 0.3s forwards;
}

@keyframes show {
  0% {
    filter: blur(5px);
    transform: translateY(calc(-50% + 75px));
  }
  100% {
    opacity: 1;
    filter: blur(0);
  }
}

.nav {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: 5;
  user-select: none;
  display: flex;
  gap: 0.5rem;
}

.nav .btn {
  background-color: rgba(255,255,255,0.5);
  color: rgba(0,0,0,0.7);
  border: 2px solid rgba(0,0,0,0.6);
  padding: 0.75rem;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s ease;
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  top: -200px;
}

.nav .btn:hover {
  background-color: rgba(255,255,255,0.3);
}

.nav .btn svg {
  width: 24px;
  height: 24px;
}

.return-btn-global {
  position: fixed;
  top: 20px;
  left: 20px;
  z-index: 1000;
  padding: 10px 16px;
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease;
}

.return-btn-global:hover {
  background-color:gold;
}



/* Медиа-запросы для адаптивности */
@media (width > 650px) and (width < 900px) {
  .content .title { 
    font-size: 1rem; 
  }
  
  .content .description { 
    font-size: 0.7rem; 
  }
  
  .content button { 
    font-size: 0.7rem; 
  }
  
  .item {
    width: 160px;
    height: 270px;
  }
  
  .item:nth-child(3) { 
    left: 50%; 
  }
  
  .item:nth-child(4) { 
    left: calc(50% + 170px); 
  }
  
  .item:nth-child(5) { 
    left: calc(50% + 340px); 
  }
  
  .item:nth-child(6) { 
    left: calc(50% + 510px); 
    opacity: 0; 
  }
}

@media (width < 650px) {
  .content .title { 
    font-size: 0.9rem; 
  }
  
  .content .description { 
    font-size: 0.65rem; 
  }
  
  .content button { 
    font-size: 0.7rem; 
  }
  
  .item {
    width: 130px;
    height: 220px;
  }
  
  .item:nth-child(3) { 
    left: 50%; 
  }
  
  .item:nth-child(4) { 
    left: calc(50% + 140px); 
  }
  
  .item:nth-child(5) { 
    left: calc(50% + 280px); 
  }
  
  .item:nth-child(6) { 
    left: calc(50% + 420px); 
    opacity: 0; 
  }
}